import React, { useEffect, useRef } from 'react';
import { protectModalState, toggleToReport, toggleToBlockUser } from '../../../store/ProtectSlice';
import './ProtectModal.scss';
import '../Modals.scss';
import { useDispatch, useSelector } from 'react-redux';
import SVGIcon from '../../../assets/icons/svgComponent';

const ProtectModal = () => {
	const dispatch = useDispatch();
	const ref = useRef<HTMLInputElement>(null);

	const userName = useSelector((state: any) => state.ProtectState.userName);

	const handleClickOutside = (e: any) => {
		if (e.target.className === ref.current?.className) {
			dispatch(protectModalState(false));
		}
	};

	useEffect(() => {
		document.addEventListener('mousedown', handleClickOutside);
		return () => document.removeEventListener('mousedown', handleClickOutside);
	});

	return (
		<div className='ProtectModal Modal' ref={ref}>
			<div className='modalWrapper'>
				<div className='tittle'>
					<h2> </h2>
				</div>
				<div className='itemWrapper'>
					<div className='item'>
						<div className='img'>
							<SVGIcon name='deleteBtn' size={25} />
						</div>
						<div className='description'>
							<div className='main'>
								<span>  : {userName}</span>
							</div>
							<div className='sub'>
								<span>   ?    .</span>
							</div>
						</div>
					</div>
					<div
						className='item'
						onClick={() => {
							dispatch(toggleToBlockUser());
						}}
					>
						<div className='img'>
							<SVGIcon name='unAvailableBtn' size={25} />
						</div>
						<div className='description'>
							<div className='main'>
								<span>  {userName}</span>
							</div>
							<div className='sub'>
								<span>      .</span>
							</div>
						</div>
					</div>
					<div
						className='item'
						onClick={() => {
							dispatch(toggleToReport());
						}}
					>
						<div className='img'>
							<SVGIcon name='flagBtn' size={25} />
						</div>
						<div className='description'>
							<div className='main'>
								<span>  {userName}</span>
							</div>
							<div className='sub'>
								<span>,  ,     .</span>
							</div>
						</div>
					</div>
					<div className='item'>
						<div className='img'>
							<SVGIcon name='shieldDone' size={25} />
						</div>
						<div className='description'>
							<div className='main'>
								<span>   </span>
							</div>
							<div className='sub'>
								<span>    .  .</span>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};
export default ProtectModal;
