const SVGIcon = ({ name, size = 24, width = size, ...rest }) => {
	const icon = icons[name];

	if (!icon) {
		console.warn(`Icon "${name}" not found`);
		return null;
	}

	return React.cloneElement(icon, {
		...rest,
		//   fill: fill,
		width: width,
		//   stroke: stroke,
		height: size
	});
};

export default SVGIcon;
